<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shangjiahao" >
					<el-input v-model="ruleForm.shangjiahao" placeholder="" clearable  :readonly="ro.shangjiahao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shangjiahao" >
					<el-input v-model="ruleForm.shangjiahao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="mima" >
					<el-input v-model="ruleForm.mima" placeholder="" clearable  :readonly="ro.mima"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="mima" >
					<el-input v-model="ruleForm.mima" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shangjiamingcheng" >
					<el-input v-model="ruleForm.shangjiamingcheng" placeholder="" clearable  :readonly="ro.shangjiamingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shangjiamingcheng" >
					<el-input v-model="ruleForm.shangjiamingcheng" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="dianhua" >
					<el-input v-model="ruleForm.dianhua" placeholder="" clearable  :readonly="ro.dianhua"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="dianhua" >
					<el-input v-model="ruleForm.dianhua" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shangjiadizhi" >
					<el-input v-model="ruleForm.shangjiadizhi" placeholder="" clearable  :readonly="ro.shangjiadizhi"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shangjiadizhi" >
					<el-input v-model="ruleForm.shangjiadizhi" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.yingyezhizhao" label="" prop="yingyezhizhao" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.yingyezhizhao?ruleForm.yingyezhizhao:''"
						@change="yingyezhizhaoUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.yingyezhizhao" label="" prop="yingyezhizhao" >
					<img v-if="ruleForm.yingyezhizhao.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.yingyezhizhao.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.yingyezhizhao.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
