<script>
	import { 
		isNumber,
		isMobile,
	} from "@/utils/validate";
	export default {
		data() {
			var validateMobile = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isMobile(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					shangjiahao : false,
					mima : false,
					shangjiamingcheng : false,
					dianhua : false,
					shangjiadizhi : false,
					yingyezhizhao : false,
					money : false,
				},
			
				ruleForm: {
					shangjiahao: '',
					mima: '',
					shangjiamingcheng: '',
					dianhua: '',
					shangjiadizhi: '',
					yingyezhizhao: '',
				},
		

				rules: {
					shangjiahao: [
						{ required: true, message: '', trigger: 'blur' },
					],
					mima: [
						{ required: true, message: '', trigger: 'blur' },
					],
					shangjiamingcheng: [
						{ required: true, message: '', trigger: 'blur' },
					],
					dianhua: [
						{ required: true, message: '', trigger: 'blur' },
						{ validator: validateMobile, trigger: 'blur' },
					],
					shangjiadizhi: [
						{ required: true, message: '', trigger: 'blur' },
					],
					yingyezhizhao: [
						{ required: true, message: '', trigger: 'blur' },
					],
					money: [
						{ validator: validateNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'){
					this.info(id);
				}else if(this.type=='logistics'){
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='shangjiahao'){
							this.ruleForm.shangjiahao = obj[o];
							this.ro.shangjiahao = true;
							continue;
						}
						if(o=='mima'){
							this.ruleForm.mima = obj[o];
							this.ro.mima = true;
							continue;
						}
						if(o=='shangjiamingcheng'){
							this.ruleForm.shangjiamingcheng = obj[o];
							this.ro.shangjiamingcheng = true;
							continue;
						}
						if(o=='dianhua'){
							this.ruleForm.dianhua = obj[o];
							this.ro.dianhua = true;
							continue;
						}
						if(o=='shangjiadizhi'){
							this.ruleForm.shangjiadizhi = obj[o];
							this.ro.shangjiadizhi = true;
							continue;
						}
						if(o=='yingyezhizhao'){
							this.ruleForm.yingyezhizhao = obj[o];
							this.ro.yingyezhizhao = true;
							continue;
						}
						if(o=='money'){
							this.ruleForm.money = obj[o];
							this.ro.money = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `shangjia/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.yingyezhizhao!=null) {
						this.ruleForm.yingyezhizhao = this.ruleForm.yingyezhizhao.replace(new RegExp(this.$base.url,"g"),"");
					}
var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `shangjia/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.shangjiaCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.shangjiaCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			yingyezhizhaoUploadChange(fileUrls) {
				this.ruleForm.yingyezhizhao = fileUrls;
			},
		}
	};
</script>
