import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
	import discusstongzhigonggao from '@/views/modules/discusstongzhigonggao/list'
	import yonghu from '@/views/modules/yonghu/list'
	import shangpinfenlei from '@/views/modules/shangpinfenlei/list'
	import chat from '@/views/modules/chat/list'
	import tongzhigonggao from '@/views/modules/tongzhigonggao/list'
	import messages from '@/views/modules/messages/list'
	import chongwushangpin from '@/views/modules/chongwushangpin/list'
	import shouhoufuwu from '@/views/modules/shouhoufuwu/list'
	import orders from '@/views/modules/orders/list'
	import discusschongwushangpin from '@/views/modules/discusschongwushangpin/list'
	import config from '@/views/modules/config/list'
	import shangjia from '@/views/modules/shangjia/list'


//2.   
export const routes = [{
	path: '/',
	name: '',
	component: Index,
	children: [{
		// main
		path: '/',
		name: '',
		component: Home,
		meta: {icon:'', title:'center', affix: true}
	}, {
		path: '/updatePassword',
		name: '',
		component: UpdatePassword,
		meta: {icon:'', title:'updatePassword'}
	}, {
		path: '/pay',
		name: '',
		component: pay,
		meta: {icon:'', title:'pay'}
	}, {
		path: '/center',
		name: '',
		component: center,
		meta: {icon:'', title:'center'}
	}
	,{
		path: '/discusstongzhigonggao',
		name: '',
		component: discusstongzhigonggao
	}
	,{
		path: '/yonghu',
		name: '',
		component: yonghu
	}
	,{
		path: '/shangpinfenlei',
		name: '',
		component: shangpinfenlei
	}
	,{
		path: '/chat',
		name: '',
		component: chat
	}
	,{
		path: '/tongzhigonggao',
		name: '',
		component: tongzhigonggao
	}
	,{
		path: '/messages',
		name: '',
		component: messages
	}
	,{
		path: '/chongwushangpin',
		name: '',
		component: chongwushangpin
	}
	,{
		path: '/shouhoufuwu',
		name: '',
		component: shouhoufuwu
	}
	,{
		path: '/orders/:status',
		name: '',
		component: orders
	}
	,{
		path: '/discusschongwushangpin',
		name: '',
		component: discusschongwushangpin
	}
	,{
		path: '/config',
		name: '',
		component: config
	}
	,{
		path: '/shangjia',
		name: '',
		component: shangjia
	}
	]
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {icon:'', title:'login'}
	},
	{
		path: '/register',
		name: 'register',
		component: register,
		meta: {icon:'', title:'register'}
	},
	{
		path: '*',
		component: NotFound
	}
]
//3.VueRouter  
const router = new VueRouter({
	mode: 'hash',
	/*hashhistory*/
	routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
export default router;
