<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="dingdanbianhao" >
					<el-input v-model="ruleForm.dingdanbianhao" placeholder="" clearable  :readonly="ro.dingdanbianhao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="dingdanbianhao" >
					<el-input v-model="ruleForm.dingdanbianhao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="leixing" >
					<el-select :disabled="ro.leixing" v-model="ruleForm.leixing" placeholder="" >
						<el-option
							v-for="(item,index) in leixingOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="leixing" >
					<el-input v-model="ruleForm.leixing"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.shangpintupian" label="" prop="shangpintupian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.shangpintupian?ruleForm.shangpintupian:''"
						@change="shangpintupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.shangpintupian" label="" prop="shangpintupian" >
					<img v-if="ruleForm.shangpintupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.shangpintupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.shangpintupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="fuwuneirong" >
					<el-input v-model="ruleForm.fuwuneirong" placeholder="" clearable  :readonly="ro.fuwuneirong"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="fuwuneirong" >
					<el-input v-model="ruleForm.fuwuneirong" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shenqingyuanyin" >
					<el-input v-model="ruleForm.shenqingyuanyin" placeholder="" clearable  :readonly="ro.shenqingyuanyin"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shenqingyuanyin" >
					<el-input v-model="ruleForm.shenqingyuanyin" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="shenqingshijian" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.shenqingshijian" 
						type="datetime"
						:readonly="ro.shenqingshijian"
						placeholder=""
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.shenqingshijian" label="" prop="shenqingshijian" >
					<el-input v-model="ruleForm.shenqingshijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="" clearable  :readonly="ro.zhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="" clearable  :readonly="ro.xingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shouji" >
					<el-input v-model="ruleForm.shouji" placeholder="" clearable  :readonly="ro.shouji"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shouji" >
					<el-input v-model="ruleForm.shouji" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shangjiahao" >
					<el-input v-model="ruleForm.shangjiahao" placeholder="" clearable  :readonly="ro.shangjiahao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shangjiahao" >
					<el-input v-model="ruleForm.shangjiahao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shangjiamingcheng" >
					<el-input v-model="ruleForm.shangjiamingcheng" placeholder="" clearable  :readonly="ro.shangjiamingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shangjiamingcheng" >
					<el-input v-model="ruleForm.shangjiamingcheng" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	export default {
		data() {
			return {
				id: '',
				type: '',
			
			
				ro:{
					dingdanbianhao : false,
					leixing : false,
					shangpintupian : false,
					fuwuneirong : false,
					shenqingyuanyin : false,
					shenqingshijian : false,
					zhanghao : false,
					xingming : false,
					shouji : false,
					shangjiahao : false,
					shangjiamingcheng : false,
					sfsh : false,
					shhf : false,
				},
			
				ruleForm: {
					dingdanbianhao: '',
					leixing: '',
					shangpintupian: '',
					fuwuneirong: '',
					shenqingyuanyin: '',
					shenqingshijian: '',
					zhanghao: '',
					xingming: '',
					shouji: '',
					shangjiahao: '',
					shangjiamingcheng: '',
					shhf: '',
				},
		
				leixingOptions: [],

				rules: {
					dingdanbianhao: [
						{ required: true, message: '', trigger: 'blur' },
					],
					leixing: [
					],
					shangpintupian: [
						{ required: true, message: '', trigger: 'blur' },
					],
					fuwuneirong: [
						{ required: true, message: '', trigger: 'blur' },
					],
					shenqingyuanyin: [
						{ required: true, message: '', trigger: 'blur' },
					],
					shenqingshijian: [
					],
					zhanghao: [
					],
					xingming: [
						{ required: true, message: '', trigger: 'blur' },
					],
					shouji: [
					],
					shangjiahao: [
					],
					shangjiamingcheng: [
					],
					sfsh: [
					],
					shhf: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'){
					this.info(id);
				}else if(this.type=='logistics'){
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='dingdanbianhao'){
							this.ruleForm.dingdanbianhao = obj[o];
							this.ro.dingdanbianhao = true;
							continue;
						}
						if(o=='leixing'){
							this.ruleForm.leixing = obj[o];
							this.ro.leixing = true;
							continue;
						}
						if(o=='shangpintupian'){
							this.ruleForm.shangpintupian = obj[o];
							this.ro.shangpintupian = true;
							continue;
						}
						if(o=='fuwuneirong'){
							this.ruleForm.fuwuneirong = obj[o];
							this.ro.fuwuneirong = true;
							continue;
						}
						if(o=='shenqingyuanyin'){
							this.ruleForm.shenqingyuanyin = obj[o];
							this.ro.shenqingyuanyin = true;
							continue;
						}
						if(o=='shenqingshijian'){
							this.ruleForm.shenqingshijian = obj[o];
							this.ro.shenqingshijian = true;
							continue;
						}
						if(o=='zhanghao'){
							this.ruleForm.zhanghao = obj[o];
							this.ro.zhanghao = true;
							continue;
						}
						if(o=='xingming'){
							this.ruleForm.xingming = obj[o];
							this.ro.xingming = true;
							continue;
						}
						if(o=='shouji'){
							this.ruleForm.shouji = obj[o];
							this.ro.shouji = true;
							continue;
						}
						if(o=='shangjiahao'){
							this.ruleForm.shangjiahao = obj[o];
							this.ro.shangjiahao = true;
							continue;
						}
						if(o=='shangjiamingcheng'){
							this.ruleForm.shangjiamingcheng = obj[o];
							this.ro.shangjiamingcheng = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.zhanghao!=''&&json.zhanghao) || json.zhanghao==0) && this.$storage.get("role")!=""){
							this.ruleForm.zhanghao = json.zhanghao
							this.ro.zhanghao = true;
						}
						if(((json.xingming!=''&&json.xingming) || json.xingming==0) && this.$storage.get("role")!=""){
							this.ruleForm.xingming = json.xingming
							this.ro.xingming = true;
						}
						if(((json.shouji!=''&&json.shouji) || json.shouji==0) && this.$storage.get("role")!=""){
							this.ruleForm.shouji = json.shouji
							this.ro.shouji = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
				this.leixingOptions = ",,".split(',')
			
			},
			// 

			info(id) {
				this.$http({
					url: `shouhoufuwu/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.shangpintupian!=null) {
						this.ruleForm.shangpintupian = this.ruleForm.shangpintupian.replace(new RegExp(this.$base.url,"g"),"");
					}
var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `shouhoufuwu/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.shouhoufuwuCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.shouhoufuwuCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			shangpintupianUploadChange(fileUrls) {
				this.ruleForm.shangpintupian = fileUrls;
			},
		}
	};
</script>
